import React, { Suspense, lazy, useEffect } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import {
  Card,
  CardBody,
  Form,
  FormGroup,
  ActionGroup,
  Checkbox} from '@patternfly/react-core'

import {
  Button,
  StackItem,
  Stack,
  Title,
  Spinner,
} from '@patternfly/react-core';
import { Main } from '@redhat-cloud-services/frontend-components/Main';
import {
  PageHeader,
  PageHeaderTitle,
} from '@redhat-cloud-services/frontend-components/PageHeader';
import { addNotification } from '@redhat-cloud-services/frontend-components-notifications/redux';

const targetAddress = "https://sso.dev1.redhat.com/auth/realms/redhat-external/protocol/openid-connect/twofact";

const SampleComponent = lazy(() =>
  import('../../Components/SampleComponent/sample-component')
);

import './sample-page.scss';

/**
 * A smart component that handles all the api calls and data needed by the dumb components.
 * Smart components are usually classes.
 *
 * https://reactjs.org/docs/components-and-props.html
 * https://medium.com/@thejasonfile/dumb-components-and-smart-components-e7b33a698d43
 */
const SamplePage = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    insights?.chrome?.appAction?.('sample-page');
  }, []);

  const handleAlert = () => {
    dispatch(
      addNotification({
        variant: 'success',
        title: 'Notification title',
        description: 'notification description',
      })
    );
  };
  
  const handleButtonClick = function (e) {
    console.log("button clicked");
    fetch(targetAddress)
      .then(response => response.json())
      .then(data => console.log(data));
  }

  return (
    <React.Fragment>
      <PageHeader>
        <PageHeaderTitle title="Admin Self Service" />
        <p> Tweak settings for your company's access to Red Hat's services </p>
      </PageHeader>
      <Main>
        <Stack hasGutter>
          <StackItem>
            <Card>
              <CardBody>
                <Form>
                  <FormGroup>
                    <Checkbox label="Enable Two Factor Authentication" id="confirmed"></Checkbox>
                  </FormGroup>
                  <ActionGroup>
                    <Button onClick={handleButtonClick}>Submit</Button>
                  </ActionGroup>
                </Form>
              </CardBody>
            </Card>
          </StackItem>
          <StackItem>
            <Suspense fallback={<Spinner />}>
              <SampleComponent />
            </Suspense>
          </StackItem>
        </Stack>
      </Main>
    </React.Fragment>
  );
};

export default withRouter(SamplePage);
